* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.wrapper {
  width: 190px;
  border: 1px solid #000;
  margin-left: auto;
  margin-right: auto;
  padding: 40px;
}

button[data-start] {
  width: 50px;
  height: 25px;
  cursor: pointer;
  background-color: #d3f3d5;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  box-shadow: inset 0 -1px 34px 40px rgba(34, 60, 80, .2);
}

button[data-stop] {
  width: 50px;
  height: 25px;
  cursor: pointer;
  background-color: #ff0442;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  box-shadow: inset 0 -1px 34px 40px rgba(34, 60, 80, .2);
}

.timer {
  justify-content: space-around;
  margin-top: 10%;
  font-size: 48px;
  display: flex;
}

